import React from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Pie } from "react-chartjs-2";
import { products } from "../UserInfo/Products";
ChartJS.register(ArcElement, Tooltip, Legend);
function Pies() {
  const data = {
    labels: products
      .sort((a, b) => b.data - a.data)
      .slice(0, 5)
      .map((item) => item.key),
    datasets: [
      {
        label: "Product",
        data: products
          .sort((a, b) => b.data - a.data)
          .slice(0, 5)
          .map((item) => item.data),
        backgroundColor: [
          "rgba(255, 99, 132, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(255, 206, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
          "rgba(153, 102, 255, 0.2)",
          "rgba(255, 159, 64, 0.2)",
        ],
        borderColor: [
          "rgba(255, 99, 132, 1)",
          "rgba(54, 162, 235, 1)",
          "rgba(255, 206, 86, 1)",
          "rgba(75, 192, 192, 1)",
          "rgba(153, 102, 255, 1)",
          "rgba(255, 159, 64, 1)",
        ],
        borderWidth: 1,
      },
    ],
  };

  return (
    <div style={{ height: "500px", width: "500px" }}>
      <Pie data={data} />
    </div>
  );
}

export default Pies;
